import React from 'react';
import './App.css';
import Content from './components/content/main';

import Preload from './components/preloader/main';
class App extends React.Component {

  constructor(props) {
  super(props);
  this.state = {
    preloaded: false,
  };
}
  componentDidMount(){
    setTimeout(() => {
      this.setState({
        preloaded:true,
      });
    }, 3000);

  }
  render(){
  return (
    <>
    <Preload action={this.state.preloaded}/>
    <Content/>
    </>
  );
}
}

export default App;
